/* eslint-disable no-nested-ternary */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import MonthPickerInput from 'react-month-picker-input';
import moment from 'moment';
import { getPaymentHistoryChefSaga } from '../../../store/actions';
import 'react-month-picker-input/dist/react-month-picker-input.css';

const PaymentHistory = props => {
  const [mounted, setMounted] = useState(false);
  const [monthYearError, setMonthYearError] = useState(false);
  const { paymentHistory, errorMsg } = useSelector(state => state.payment);

  const dispatch = useDispatch();

  const getData = (month, year) => {
    dispatch(getPaymentHistoryChefSaga({ month: month + 1, year }));
  };

  useEffect(() => {
    setMounted(true);
    getData(new Date().getMonth(), new Date().getFullYear());
    return () => setMounted(false);
  }, []);

  const monthDiff = (d1, d2) => {
    let months;
    months = (d2.getFullYear() - d1.getFullYear()) * 12;
    months -= d1.getMonth();
    months += d2.getMonth();
    return months <= 0 ? 0 : months;
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Payout History</h2>
              </div>
              <div className="row m-b-sm">
                <div className="col-sm-12 col-md-12 col-lg-12">
                  <h4>Select Date:</h4>
                  <div className="row">
                    <div className="col-sm-9 col-md-9 col-lg-6">
                      <div className="mb-10" id="data_5">
                        <MonthPickerInput
                          inputProps={{ className: 'form-control' }}
                          year={new Date().getFullYear()}
                          month={new Date().getMonth()}
                          maxDate={[11, 9999]}
                          showMonthYearPicker
                          onChange={(
                            maskedValue,
                            selectedYear,
                            selectedMonth,
                          ) => {
                            const currentDate = new Date();
                            const dateDifference = monthDiff(
                              new Date(selectedYear, selectedMonth - 1, 1),
                              new Date(
                                currentDate.getFullYear(),
                                currentDate.getMonth(),
                                1,
                              ),
                            );
                            if (dateDifference > 0) {
                              setMonthYearError(false);
                              getData(selectedMonth, selectedYear);
                            } else {
                              setMonthYearError(true);
                            }
                          }}
                        />
                        <div className="error-message">
                          {monthYearError &&
                            'Selected month and year must be current or previous'}
                        </div>
                      </div>
                    </div>
                    {/* <div className="col-sm-3 col-md-3 col-lg-6 text-right">
                      <button className="btn btn-primary " type="submit">
                        Download CSV
                      </button>
                    </div> */}
                  </div>
                </div>
              </div>
              {paymentHistory[0] ? (
                <>
                  <h3>
                    {`1st Cycle (01-${paymentHistory[0].month}-${paymentHistory[0].year} to
                   15-${paymentHistory[0].month}-${paymentHistory[0].year})`}
                  </h3>
                  <div className="row">
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              A
                            </button>
                            Commission
                          </h5>
                          <p>{`₹${paymentHistory[0].commission}`}</p>
                          {/* <p className="my-name">
                            Number of Orders:
                            {paymentHistory[0].orderCount}
                          </p> */}
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              B
                            </button>
                            Platform fee - Monthly
                          </h5>
                          <p>{`₹${paymentHistory[0].chefPlatformFee}`}</p>

                          <p className="my-name text-right">
                            {/* <NavLink to="/vendors/payment-history-detail">
                          View Details
                        </NavLink> */}
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              C
                            </button>
                            GST on commission
                          </h5>
                          <p>{`₹${paymentHistory[0].gst}`}</p>

                          {/* <p className="my-name">
                            Number of Orders:
                            {paymentHistory[0].orderCount}
                          </p> */}
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="row m-t-sm">
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              D
                            </button>
                            TCS amount
                          </h5>
                          <p>{`₹${paymentHistory[0].tcs}`}</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              E
                            </button>
                            Transaction fee
                          </h5>
                          <p>{`₹${paymentHistory[0].paymentGateway}`}</p>

                          <p className="my-name text-right">
                            {/* <NavLink to="/vendors/payment-history-detail">
                          View Details
                        </NavLink> */}
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              F
                            </button>
                            Total payable amount
                          </h5>
                          <p>{`₹${paymentHistory[0].amount}`}</p>
                          <p className="my-name text-right">
                            {/* <NavLink to="/vendors/payment-history-detail">
                          View Details
                        </NavLink> */}
                          </p>
                        </div>
                      </div>
                    </div>
                    <h4 style={{ marginLeft: '15px' }}>
                      Total amount for the 1st cycle is
                      {` ₹${paymentHistory[0].amount}`}
                    </h4>
                  </div>
                </>
              ) : (
                <div className="error-card-div">{errorMsg}</div>
              )}
              {paymentHistory[1] ? (
                <>
                  <h3>
                    {`2nd Cycle (16-${paymentHistory[0].month}-${
                      paymentHistory[0].year
                    } to 
          ${moment([paymentHistory[0].year, paymentHistory[0].month - 1])
            .endOf('month')
            .format('DD')}-
          ${paymentHistory[0].month}-${paymentHistory[0].year})`}
                  </h3>
                  <div className="row">
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              A
                            </button>
                            Commission
                          </h5>
                          <p>{`₹${paymentHistory[1].commission}`}</p>
                          {/* <p className="my-name">
                            Number of Orders:
                            {paymentHistory[1].orderCount}
                          </p> */}
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              B
                            </button>
                            Platform fee - Monthly
                          </h5>
                          <p>{`₹${paymentHistory[1].chefPlatformFee}`}</p>

                          <p className="my-name text-right">
                            {/* <NavLink to="/vendors/payment-history-detail">
                          View Details
                        </NavLink> */}
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              C
                            </button>
                            GST on commission
                          </h5>
                          <p>{`₹${paymentHistory[1].gst}`}</p>
                          {/* <p className="my-name">
                            Number of Orders:
                            {paymentHistory[1].orderCount}
                          </p> */}
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="row m-t-sm">
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              D
                            </button>
                            TCS amount
                          </h5>
                          <p>{`₹${paymentHistory[1].tcs}`}</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              E
                            </button>
                            Transaction fee
                          </h5>
                          <p>{`₹${paymentHistory[1].paymentGateway}`}</p>

                          <p className="my-name text-right">
                            {/* <NavLink to="/vendors/payment-history-detail">
                          View Details
                        </NavLink> */}
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="ibox float-e-margins">
                        <div className="ibox-title">
                          <h5>
                            <button
                              type="button"
                              className="btn-primary btn"
                              style={{
                                padding: '5px 10px',
                                marginRight: '10px',
                              }}
                            >
                              F
                            </button>
                            Total payable amount
                          </h5>
                          <p>{`₹${paymentHistory[1].amount}`}</p>
                          <p className="my-name text-right">
                            {/* <NavLink to="/vendors/payment-history-detail">
                          View Details
                        </NavLink> */}
                          </p>
                        </div>
                      </div>
                    </div>
                    <h4 style={{ marginLeft: '15px' }}>
                      Total amount for the 2nd cycle is
                      {` ₹${paymentHistory[1].amount}`}
                    </h4>
                  </div>
                </>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PaymentHistory;
